import React from "react";
import { Card, Row, Button, Input } from "antd";
import Notification from "../common/notification";
import APIService from "../common/api";

class SomePage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      email: null,
      password: null,
      data: null,
    };
  }

  handleChangeUserAdd = (e) => {
    const { name, value } = e.target;
    this.setState({
      [name]: value,
    });
  };

  login = () => {
      const data = {
          email:this.state.email,
          password:this.state.password
      }
    APIService.loginandattend(data)
      .then((x) => {
        Notification.success("Attendence Marked successfully.");
        this.setState({ data: x.data });
        console.log(this.state.data)
      })
      .catch((err) => {
        Notification.error("Invalid Credentials.");
      });
  };
  render() {
    return (
      <div style={{ margin: "auto" }}>
        <Card title={this.state.data !== null ? 'Details' : 'Login'} style={{ width: "100%" }}>
          {this.state.data !== null ? (
            <Row>
              <div style={{ margin: "15px 0px" }}>
                <label>Full Name: {this.state.data.name}</label>
              </div>
              <div style={{ margin: "15px 0px" }}>
                <label>Father Name: {this.state.data.fatherName}</label>
              </div>
              <div style={{ margin: "15px 0px" }}>
                <label>Grade: {this.state.data.grade}</label>
              </div>
              <div style={{ margin: "15px 0px" }}>
                <label>Total Presents: {this.state.data.totalPresence}</label>
              </div>
            </Row>
          ) : (
            <div>
              <Row>
                <div style={{ margin: "15px 0px" }}>
                  <label>Email</label>
                  <Input
                    placeholder="Enter Email"
                    type="email"
                    value={this.state.email}
                    name="email"
                    onChange={this.handleChangeUserAdd}
                  />
                </div>
                <div style={{ margin: "15px 0px" }}>
                  <label>Password</label>
                  <Input
                    placeholder="Enter Password"
                    type="password"
                    value={this.state.password}
                    name="password"
                    onChange={this.handleChangeUserAdd}
                  />
                </div>
              </Row>
              <Button onClick={this.login} type="primary">
                Login
              </Button>
            </div>
          )}
        </Card>
      </div>
    );
  }
}

export default SomePage;
