import React from "react";
import "antd/dist/antd.css";
import { Table, Icon, Input, Modal } from "antd";
import QRCode from 'qrcode.react'

const UserTable = ({
  data,
  editfunc,
  delefunc,
  handleChange,
  editIdx,
  checkfunc,
}) => {
  const [visible, setvisible] = React.useState(false);

  const getUserTableColumn = () => {
    return [
      {
        title: "Name",
        dataIndex: "name",
        render: (text, record) => <span>{text}</span>,
      },
      {
        title: "Father Name",
        dataIndex: "fatherName",
        render: (text, record) =>
          record.name === editIdx ? (
            <Input
              name="name"
              defaultValue={text}
              onChange={(event) => handleChange(event, record)}
            />
          ) : (
            <span>{text}</span>
          ),
      },
      {
        title: "CNIC",
        dataIndex: "cnic",
        render: (text, record) =>
          record.name === editIdx ? (
            <Input
              name="age"
              defaultValue={text}
              onChange={(event) => handleChange(event, record)}
            />
          ) : (
            <span>{text}</span>
          ),
      },
      {
        title: "Grade",
        dataIndex: "grade",
        render: (text, record) =>
          record.name === editIdx ? (
            <Input
              name="grade"
              defaultValue={text}
              onChange={(event) => handleChange(event, record)}
            />
          ) : (
            <span>{text}</span>
          ),
      },
      {
        title: "Join Date",
        dataIndex: "joinDate",
        render: (text, record) =>
          record.name === editIdx ? (
            <Input
              name="joinDate"
              defaultValue={text}
              onChange={(event) => handleChange(event, record)}
            />
          ) : (
            <span>{text.split("T")[0]}</span>
          ),
      },
      {
        title: "Allownaces",
        dataIndex: "allownaces",
        render: (text, record) =>
          record.name === editIdx ? (
            <Input
              name="allownaces"
              defaultValue={text}
              onChange={(event) => handleChange(event, record)}
            />
          ) : (
            <span>{text}</span>
          ),
      },
      {
        title: "Allowed Leaves",
        dataIndex: "allowedLeaves",
        render: (text, record) =>
          record.key === editIdx ? (
            <Input
              name="allowedLeaves"
              defaultValue={text}
              onChange={(event) => handleChange(event, record)}
            />
          ) : (
            <span>{text}</span>
          ),
      },
      {
        title: "Action",
        dataIndex: "",
        key: "x",
        render: (text, record) => {
          return (
            <div>
              {/* {record.key === editIdx  ? 
                                    <Icon type="check" onClick={() => checkfunc(text,record)} /> : 
                                    <Icon type="edit" onClick={(e) => editfunc(text,record)} />
                                } |  */}
              <Icon
                style={{ fontSize: 30, marginRight: 10 }}
                type="delete"
                onClick={() => delefunc(text, record)}
              />
              <Icon
                style={{ fontSize: 30 }}
                type="qrcode"
                onClick={() => setvisible(true)}
              />
            </div>
          );
        },
      },
    ];
  };

  return (
    <React.Fragment>
      <Modal
        title="QR Code"
        visible={visible}
        onCancel={() => setvisible(false)}
        footer={[]}
      >

    <QRCode style={{width:'40%',marginLeft:'30%',height:'none'}} value="http://facebook.github.io/react/" />

      </Modal>

      <Table
        columns={getUserTableColumn()}
        dataSource={data || []}
        size="middle"
      />
    </React.Fragment>
  );
};

export default UserTable;
