import React from "react";
import { Row, Button, Input } from "antd";
import APIService from "../common/api";
import Notification from "../common/notification";

class AddUserComponent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      name: null,
      fatherName: null,
      cnic: null,
      grade: null,
      joinDate: null,
      allownaces: null,
      allowedLeaves: null,
      email:null,
      password:null
    };
  }
  saveUserDataAPIFun = (data) => {
    try {
      APIService.saveUserDataAPI(data).then(
        (res) => {
          console.log("response-> ", res);
          this.props.tableListUpdate();
          Notification.success("User data edited successfully.");
        },
        (err) => {
          console.log(err);
          Notification.error("Something went wrong.");
        }
      );
    } catch (error) {
      console.log(error);
      Notification.error("Error");
    }
  };
  handleChangeUserAdd = (e) => {
    const { name, value } = e.target;
    this.setState({
      [name]: value,
    });
  };
  resetState = () => {
    this.setState({
      name: null,
      fatherName: null,
      cnic: null,
      grade: null,
      joinDate: null,
      allownaces: null,
      allowedLeaves: null,
      email:null,
      password:null
    });
  };
  addUserSubmitFunc = () => {
    const data = {
      name: this.state.name,
      fatherName: this.state.fatherName,
      cnic: this.state.cnic,
      grade: this.state.grade,
      joinDate: this.state.joinDate,
      allownaces: this.state.allownaces,
      allowedLeaves: this.state.allowedLeaves,
      email:this.state.email,
      password:this.state.password
    };
    this.saveUserDataAPIFun(data);
    this.resetState();
    this.props.closeWindow();
  };
  render() {
    return (
      <React.Fragment>
        <Row>
          <p>Please add the following details for adding the new employee.</p>
          <div style={{ margin: "15px 0px" }}>
            <label>Name:</label>
            <Input
              placeholder="Enter Name"
              value={this.state.name}
              name="name"
              onChange={this.handleChangeUserAdd}
            />
          </div>
          <div style={{ margin: "15px 0px" }}>
            <label>Father Name</label>
            <Input
              placeholder="Enter Father Name"
              value={this.state.fatherName}
              name="fatherName"
              onChange={this.handleChangeUserAdd}
            />
          </div>
          <div style={{ margin: "15px 0px" }}>
            <label>CNIC</label>
            <Input
              placeholder="Enter CNIC"
              value={this.state.cnic}
              name="cnic"
              onChange={this.handleChangeUserAdd}
            />
          </div>
          <div style={{ margin: "15px 0px" }}>
            <label>Grade</label>
            <Input
              placeholder="Enter Grade"
              value={this.state.grade}
              name="grade"
              onChange={this.handleChangeUserAdd}
            />
          </div>
          <div style={{ margin: "15px 0px" }}>
            <label>Join Date</label>
            <Input
              placeholder="Enter Join Date"
              type="date"
              value={this.state.joinDate}
              name="joinDate"
              onChange={this.handleChangeUserAdd}
            />
          </div>
          <div style={{ margin: "15px 0px" }}>
            <label>Allowances</label>
            <Input
              placeholder="Enter Allowances"
              value={this.state.allownaces}
              name="allownaces"
              onChange={this.handleChangeUserAdd}
            />
          </div>
          <div style={{ margin: "15px 0px" }}>
            <label>Allowed Leaves</label>
            <Input
              placeholder="Enter Allowed Leaves"
              type="number"
              value={this.state.allowedLeaves}
              name="allowedLeaves"
              onChange={this.handleChangeUserAdd}
            />
          </div>
          <div style={{ margin: "15px 0px" }}>
            <label>Email</label>
            <Input
              placeholder="Enter Email"
              type="email"
              value={this.state.email}
              name="email"
              onChange={this.handleChangeUserAdd}
            />
          </div>
          <div style={{ margin: "15px 0px" }}>
            <label>Password</label>
            <Input
              placeholder="Enter Password"
              type="password"
              value={this.state.password}
              name="password"
              onChange={this.handleChangeUserAdd}
            />
          </div>
        </Row>
        <div className="addUser_box_footer">
          <Button onClick={this.props.closeWindow} style={{ marginRight: 8 }}>
            Cancel
          </Button>
          <Button onClick={this.addUserSubmitFunc} type="primary">
            Submit
          </Button>
        </div>
      </React.Fragment>
    );
  }
}

export default AddUserComponent;
