import axios from "axios";
window.axios = axios;

const userDataListAPIURL = "/api/userListFetch";
const saveUserDataAPIURL = "/api/saveDataApi";
const saveEditedDataAPIURL = "/api/udpateRecord";
const deleteDataAPIURL = "/api/deleteRecord";

export default class RestApi {
  static getUserList() {
    return axios.get("https://emppostgre.herokuapp.com/emp/getAll");
  }
  static saveUserDataAPI(data) {
    return axios.post("https://emppostgre.herokuapp.com/emp/add", data);
  }
  static saveEditedDataAPI(rowData) {
    let data = {
      name: rowData.name,
      age: rowData.age,
      address: rowData.address,
      key: rowData.key,
    };
    return axios.post(saveEditedDataAPIURL, data);
  }
  static deleteDataAPI(rowData) {
    return axios.delete(`https://emppostgre.herokuapp.com/emp/delete/${rowData.id}`);
  }

  static loginandattend(data){
    return axios.post(`https://emppostgre.herokuapp.com/emp/login`,data);
  }
}
